* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.details {
  position: fixed;
  top: 50px;
  right: 50px;
  padding: 30px;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 10px;
  font-size: 18px;
  color: white;
  text-align: center;
}

.details a {
  text-decoration: none;
  font-size: 15px;
  font-weight: bolder;
  padding: 10px;
  border: 2px solid black;
  border-radius: 10px;
  background-color: rgba(255, 0, 0, 0.76);
  color: #fff;
}

.links {
  display: block;
  margin: 1rem;
}
